body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  height: 100vh;
  background-color: #5f8eab;
}

.calculator {
  position: relative;
  min-width: 400px;
  max-width: 560px;
  border-radius: 5px;
  padding: 10px;
  border: 3px solid rgba(255, 255, 255, 0.1);
  border-bottom: 8px solid rgba(255, 255, 255, 0.1);
}

.screen {
  -ms-flex-align: center;
      align-items: center;
  overflow: hidden;
  background-color: rgba(255, 255, 255, 0.3);
  border-bottom: 5px solid rgba(0, 0, 0, 0.1);
  border-radius: 3px;
  color: white;
  display: -ms-flexbox;
  display: flex;
  height: 65px;
  -ms-flex-pack: end;
      justify-content: flex-end;
  padding: 0px 10px;
  width: 100%;
  font-size: 25px;
}

.screen .compute {
  margin: 0;
  margin-top: 25px;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  white-space: nowrap;
}

.result {
  overflow: hidden;
  width: 70%;
  text-align: right;
  position: absolute;
  right: 18px;
  top: 0px;
  color: #c4d5e0;
  margin-bottom: 25px;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  white-space: nowrap;
}

.keys {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  padding-top: 10px;
}

.d-flex { display: -ms-flexbox; display: flex; }

.key {
  border: none;
  border-radius: 2px;
  padding: 5px;
  margin: 2px;
  width: 100%;
  height: 60px;
  background-color: rgba(255, 255, 255, 0.1);
  color: white;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  font-size: 25px;
  -webkit-transition: 0.1s;
  -o-transition: 0.1s;
  transition: 0.1s;
  border-top: 0px solid #5f8eab;
  border-bottom: 5px solid rgba(0, 0, 0, 0.15);
  cursor: default;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.key:focus { outline: none; }

.reset {
  margin: 0;
  width: 50px;
  height: auto;
  margin-right: 10px;
}

.key:hover, .reset:hover {
  border-top: 1px solid rgba(94, 141, 171, 1);
  border-bottom: 4px solid rgba(0, 0, 0, 0.15);
}

.key:active, .reset:active {
  border-top: 3px solid rgba(0, 0, 0, 0.09);
  border-bottom: 0px solid rgba(0, 0, 0, 0.15);
}



